  border: 0 none;
  outline: none;
  background: transparent;
}

/**
 * Set preferred cursor
 * Remove default tap color on iOS
 */
a {
  cursor: pointer;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/**
 * Remove default tap color on iOS
 */
