input[type=reset],
input[type=button],
button {
  padding: 0;
  border: 0;
  background: none;
  font: inherit;
  line-height: normal;
  color: inherit;
  *overflow: visible;
  /* 4 */
  cursor: pointer;
  /* 3 */
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
