/**
 * Set preferred font properties across the board
 * Note: Newer descriptor `font-display` utilizes `swap` (also see Google Webmasters preferred options).
 * By doing so, this enables the font face an extremely small block period and an infinite swap period.
 * Liken this to that of the progressive option for images in that it renders a fallback font face until
 * the primary is available. If the primary font face is loaded during the font face display timeline,
 * it will swap to that.
 */
* {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-variant: normal;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: normal;
  font-size: 14px;
  font-display: swap;
}

